@import url('https://fonts.googleapis.com/css2?family=Arvo:wght@700&family=Exo+2:ital,wght@1,700&family=Lato:wght@900&family=Montserrat:wght@600;800&family=Work+Sans:wght@300&display=swap');

.App {
  min-height: 100vh;
  display: flex;
  background-image: url('./uu.png');
  background-size: cover;
  background-position: center;
}
::-webkit-scrollbar {
  width: 0px;
}

::-webkit-scrollbar-thumb {
  background: rgba(136, 136, 136, 0.281);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}